.socials {
  margin: 20px 0;

  @media (max-width: $mobile-width-only) {
    margin: 7px 0 40px 0;
  }

  & a {
    fill: #cdcdcd;

    &:focus,
    &:hover {
      fill: $mainColor;
    }
  }
}

.socials ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 112px;
  height: 28px;
}
