.footer {
  display: flex;
  flex-direction: column;
}

.footer__main-block {
  display: flex;
  justify-content: space-between;

  @media (min-width: $desktop-width) {
    flex-direction: row;
    flex-wrap: nowrap;
    padding: 65px 0 40px 0;
  }

  @media (max-width: $tablet-width-only) {
    flex-direction: column;
  }
}

.footer__socials {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: space-between;
  height: 160px;

  @media (min-width: $desktop-width) {
    padding: 0 30px 0 13.2%;
  }

  @media (max-width: $tablet-width-only) {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    height: 96px;
    margin: 0;
    padding: 0 3.3%;

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: #626962;
      opacity: 0.2;
    }
  }

  @media (max-width: $mobile-width-only) {
    box-sizing: border-box;
    flex-direction: column;
    height: 261px;
    padding: 46px 5% 13px;
  }
}

.footer__info {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  padding: 0 13.2% 0 0;
  justify-content: space-between;

  font-size: 15px;
  line-height: 25px;
  color: #1b2d37;

  @media (min-width: $desktop-width) {
    max-width: 1117px;
  }

  @media (max-width: $tablet-width-only) {
    justify-content: space-around;
    padding: 38px 10px 36px 0;
  }

  @media (max-width: $mobile-width-only) {
    flex-direction: column;
    max-width: 767px;

    padding: 0;
  }

  & a:focus,
  & a:hover {
    color: $mainColor;
  }
}

.footer__info h3 {
  margin: 0 0 25px 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: normal;
  line-height: 16px;
  font-size: 15px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #1b2d37;

  @media (max-width: $mobile-width-only) {
    margin: 28px 0 31px;
  }
}

.footer__info ul {
  margin: 0;
  padding: 0;
  list-style: none;
  color: #1b2d37;
  opacity: 0.5;

  @media (max-width: $mobile-width-only) {
    max-width: 272px;
    margin: 0 0 25px 0;
  }
}

.footer address {
  font-size: 15px;
  opacity: 0.5;
  color: #1b2d37;
  font-style: normal;
}

.footer__info a {
  color: #1b2d37;
}

.footer__rights a:focus,
.footer__rights a:hover {
  color: $mainColor;
}

.footer__rights {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-between;

  @media (min-width: $desktop-width) {
    padding: 0 13.2%;
  }

  @media (max-width: $tablet-width-only) {
    padding: 0 3.3%;
  }

  @media (max-width: $mobile-width-only) {
    flex-direction: column;
    align-items: center;
    height: 100px;
    margin: 0;
    padding: 0;
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #626962;
    opacity: 0.2;
  }
}

.footer__rights p {
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #1b2d37;
  opacity: 0.5;
  text-align: left;

  @media (max-width: $tablet-width-only) {
    padding: 10px 0;
  }

  @media (max-width: $mobile-width-only) {
    padding: 0;
    margin: 27px 0 0;
    max-width: 300px;
    text-align: center;
  }
}

.footer__only-desktop {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  width: 456px;

  @media (max-width: $tablet-width-only) {
    display: none;
  }
}

.footer__not-only-desktop {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 816px;
  margin-left: 40px;

  @media (max-width: $tablet-width-only) {
    margin: 0;
  }

  @media (max-width: $mobile-width-only) {
    width: 290px;
    justify-content: center;
  }
}

.footer__rights a {
  color: #1b2d37;
}

.footer__tablet {
  margin-left: 47px;

  @media (min-width: $desktop-width) {
    display: none;
  }

  @media (max-width: $mobile-width-only) {
    margin: 0;
  }
}

.footer__tablet p {
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #1b2d37;
  opacity: 0.5;
}
