.button {
  box-sizing: border-box;

  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border-radius: 5px;
}

.button--header {
  max-width: 200px;
  min-width: 180px;
  align-self: center;
  padding: 14px 26px;
  color: #ffffff;
  background-color: transparent;
  border: 1px solid #ffffff;

  &:hover,
  &:focus {
    color: #1b2d37;
    background-color: #ffffff;
  }

  @media (max-width: $tablet-width-only) {
    display: none;
  }
}

.button--accent {
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
  text-align: center;
  background-color: $mainColor;
  border: 1px solid $mainColor;

  &:hover,
  &:focus {
    color: $mainColor;
    background-color: #ffffff;
    border: 1px solid #ffffff;
  }
}

.button--about {
  padding: 14px 48px;
  color: $mainColor;
  border: 1px solid $mainColor;

  &:hover,
  &:focus {
    color: #ffffff;
    background-color: $mainColor;
  }
}
